import { put, takeLatest, call, all } from 'redux-saga/effects';
import AS from '../../services/apis/as';
import LoginActionTypes from './login.types';
import { actionCreator } from '../../utils/generators';
import history from '../../utils/history';
import { sagaWithLoader } from '../../redux/template/sagaWithLoader';
import MessagesActionTypes from 'redux/messages/messages.types';
import { t } from '../../i18n';

function* loginFeature(loginAction) {
  try {
    const loggedUser = yield call(new AS().login, loginAction.payload);
    sessionStorage.setItem('token', loggedUser.data.token);
    yield put({
      type: actionCreator('res', LoginActionTypes.LOGIN_ACTION),
    });
    history.push('/dashboard');
  } catch (error) {
    yield all([
      put(error),
      put({ type: actionCreator('fail', LoginActionTypes.LOGIN_ACTION) }),
    ]);
  }
}

function* logout() {
  try {
    yield call(new AS().logout);
    yield put({ type: actionCreator('res', LoginActionTypes.LOGOUT_ACTION) });
  } catch (error) {
    yield put({ type: actionCreator('fail', LoginActionTypes.LOGOUT_ACTION) });
  } finally {
    sessionStorage.removeItem('token');
    history.push('/login');
  }
}

function* resetPassword(resetPasswordAction) {
  try {
    const response = yield call(
      new AS().resetPassword,
      resetPasswordAction.payload,
    );
    yield put({
      type: actionCreator('res', LoginActionTypes.RESET_PASSWORD),
      payload: response,
    });
  } catch (error) {
    yield all([
      put({
        type: actionCreator('fail', LoginActionTypes.RESET_PASSWORD),
        payload: error.payload,
      }),
    ]);
  }
}
function* confirmResetPassword(confirmResetPasswordAction) {
  try {
    yield call(
      new AS().confirmResetPassword,
      confirmResetPasswordAction.payload,
    );
    yield all([
      put({
        type: LoginActionTypes.TOGGLE_RESET_PASSWORD_MODAL,
      }),
      put({
        type: MessagesActionTypes.SHOW_MESSAGE,
        payload: {
          severity: 'CONFIRM',
          text: t('Login:resetPassword.successMessage'),
        },
      }),
    ]);
  } catch (error) {
    yield all([
      put({
        type: actionCreator('fail', LoginActionTypes.CONFIRM_RESET_PASSWORD),
        payload: error.payload,
      }),
    ]);
  }
}
export function* loginSagas() {
  yield takeLatest(
    actionCreator('req', LoginActionTypes.LOGIN_ACTION),
    sagaWithLoader,
    loginFeature,
  );
  yield takeLatest(
    actionCreator('req', LoginActionTypes.LOGOUT_ACTION),
    logout,
  );
  yield takeLatest(
    actionCreator('req', LoginActionTypes.RESET_PASSWORD),
    sagaWithLoader,
    resetPassword,
  );
  yield takeLatest(
    actionCreator('req', LoginActionTypes.CONFIRM_RESET_PASSWORD),
    sagaWithLoader,
    confirmResetPassword,
  );
}
