import styled from 'styled-components';
export const ModalBodyContainer = styled.div.attrs(props => ({
  className: `modal fade in ${props.show ? 'modal-show' : ''}`,
}))`
  display: ${props => (props.show ? 'block' : '')};
  padding-right: 17px;
  z-index: 9999;
`;

export const IconContainer = styled.div.attrs(props => {
  let className = '';
  switch (props.type) {
    case 'ERROR':
      className = 'text-danger';
      break;
    case 'INFO':
    case 'CONFIRM':
      className = 'text-success';
      break;
    case 'WARN':
      className = 'text-warning';
    default:
      break;
  }
  return {
    className: className,
  };
})``;

export const TextContainer = styled.div.attrs(props => {
  let className = '';
  switch (props.type) {
    case 'ERROR':
      className = 'content-error';
      break;
    default:
      break;
  }
  return {
    className: className,
  };
})``;

export const Icon = styled.span.attrs(props => {
  let className = 'modal-main-icon mdi';
  switch (props.type) {
    case 'ERROR':
      className = `${className} mdi-alert-triangle`;
      break;
    case 'INFO':
    case 'CONFIRM':
      className = `${className} mdi-check`;
      break;
    case 'WARN':
      className = `${className} mdi-alert-triangle`;
      break;
    default:
      break;
  }
  return {
    className: className,
  };
})``;

export const CancelBtn = styled.button.attrs(props => {
  let className = 'btn-space btn';
  if (props.buttonPosition === 'FOOTER') {
    className = `${className} btn-default`;
  } else {
    switch (props.type) {
      case 'ERROR':
        className = `${className} btn-danger`;
        break;
      case 'INFO':
      case 'CONFIRM':
        className = `${className} btn-success`;
        break;
      case 'WARN':
        className = `${className} btn-warning`;
        break;
      default:
        break;
    }
  }

  return {
    className: className,
  };
})``;

