//@flow
import React from 'react';
import {
  ModalBodyContainer,
  TextContainer,
  Icon,
  CancelBtn,
  ValidateBtn,
  ModalFooterContainer,
  IconContainer,
} from './Modal.styles';
import BackDrop from '../BackDrop/BackDrop';
import { t } from '../../../i18n';
type Props = {
  show: boolean,
  handleClose: Function,
  text: string,
  title: string,
  type: 'ERROR' | 'CONFIRM' | 'INFO' | 'WARN',
  handleValidate?: Function,
  validateTitle: string,
  cancelTitle: string,
  hasCancelBtn?: boolean,
  hasValidateBtn?: boolean,
  buttonPosition: 'BODY' | 'FOOTER',
};

const ModalButtons = (props: Props) => (
  <>
    {props.hasCancelBtn && (
      <CancelBtn
        type={props.type}
        data-text="cancel"
        onClick={props.handleClose}
        buttonPosition={props.buttonPosition}
      >
        <span>{props.cancelTitle}</span>
      </CancelBtn>
    )}
    {props.hasValidateBtn && (
      <ValidateBtn
        type={props.type}
        data-text="validate"
        onClick={props.handleValidate}
        buttonPosition={props.buttonPosition}
      >
        <span>{props.validateTitle}</span>
      </ValidateBtn>
    )}
  </>
);

const Modal = (props: Props) => (
  <>
    <BackDrop show={props.show} clicked={props.handleClose} />
    <ModalBodyContainer
      id="mod-danger"
      tabindex="-1"
      role="dialog"
      show={props.show}
    >
      <div className="modal-dialog">
        <div className="modal-content">
          <div className="modal-header">
            <button
              className="close"
              type="button"
              data-dismiss="modal"
              aria-hidden="true"
              onClick={props.handleClose}
            >
              <span className="mdi mdi-close" />
            </button>
          </div>

          <div className="modal-body">
            <div className="text-center">
              <IconContainer type={props.type}>
                <Icon type={props.type} />
              </IconContainer>
              <TextContainer type={props.type}>
                <h3>{props.title}</h3>
                <p>{props.text}</p>
              </TextContainer>
              {props.buttonPosition === 'BODY' && (
                <div className="xs-mt-50">
                  <ModalButtons {...props} />
                </div>
              )}
            </div>
          </div>
          <ModalFooterContainer type={props.type}>
            {props.buttonPosition === 'FOOTER' && (
              <div className="xs-mt-0 d-flex justify-content-center w-100">
                <ModalButtons {...props} />
              </div>
            )}
          </ModalFooterContainer>
        </div>
      </div>
    </ModalBodyContainer>
  </>
);

Modal.defaultProps = {
  validateTitle: t('Components:modal.validateBtnTitle'),
  cancelTitle: t('Components:modal.cancelBtnTitle'),
  buttonPosition: 'BODY',
};

export default Modal;
