import { put, takeLatest, call, select, all } from 'redux-saga/effects';
import { actionCreator } from '../../utils/generators';
import { getSelectedAccountId } from '../accounts/accounts.selectors';
import Accounts from '../../services/apis/accounts';
import types from './operations.types';
import { sagaWithLoader } from '../../redux/template/sagaWithLoader';
import MessagesActionTypes from '../../redux/messages/messages.types';

function* getOperations(action) {
  try {
    const operations = yield call(
      new Accounts().getOperations,
      yield select(getSelectedAccountId),
    );
    yield put({
      type: actionCreator('res', types.GET_OPERATIONS),
      payload: operations,
    });
  } catch (error) {
    yield all([
      put({ type: MessagesActionTypes.SHOW_MESSAGE, payload: error }),
      put({
        type: actionCreator('fail', types.GET_OPERATIONS),
      }),
    ]);
  }
}

export function* operationsSagas() {
  yield takeLatest(
    actionCreator('req', types.GET_OPERATIONS),
    sagaWithLoader,
    getOperations,
  );
}
