//@flow
import * as React from 'react';
import CardTitle from '../SideBarCard/CardTitle/CardTitle';
import SideDashSVG from '../../../../assets/SvgComponents/SideBarSvg/SideDashSVG';
import SideBarCompte from '../../../../assets/SvgComponents/SideBarSvg/SideBarComptesSvg';
import SideBarCarteSvg from '../../../../assets/SvgComponents/SideBarSvg/SideBarCarteSvg';
import SideBarSimSvg from '../../../../assets/SvgComponents/SideBarSvg/SideBarSimSvg';
import SideBarTranSvg from '../../../../assets/SvgComponents/SideBarSvg/SideBarTranSvg';
import CardElements from '../SideBarCard/CardElements/CardElements';
import ItemSub from '../SubElements/ItemSub';
import CardItems from '../SideBarCard/CardItems/CardItems';
type Props = {
  key?: string,
  isNode: boolean,
  title: string,
  link?: string,
  onClick?: Function,
  icon?: string,
  order: number,
  subMenus: Array<any>,
};
const icons = {
  dashBoard: SideDashSVG,
  accounts: SideBarCompte,
  cards: SideBarCarteSvg,
  transactions: SideBarTranSvg,
  simulation: SideBarSimSvg,
};
const subItems = menu => (
  <MenuItem
    isNode={menu.isNode}
    icon={menu.icon}
    title={menu.title}
    link={menu.link}
    subMenus={menu.subMenus}
    order={menu.order}
    onClick={menu.onClick}
    key={menu.key}
  />
);
const FirstOrderMenuItem = (props: Props) => {
  const IconTag = props.icon ? icons[props.icon] : null;
  return (
    <li key={props.key}>
      <CardTitle title={props.title} link={props.link}>
        {IconTag && <IconTag />}
      </CardTitle>
      {props.isNode && props.subMenus.map(menu => subItems(menu))}
    </li>
  );
};
const SecondOrderMenuItem = (props: Props) => (
  <CardElements subtitle={props.title} key={props.key}>
    {props.isNode && props.subMenus.map(menu => subItems(menu))}
  </CardElements>
);
const ThirdOrderMenuItem = (props: Props) => (
  <CardItems
    subtitle={props.title}
    link={props.link}
    class="submenu-accordion_title"
  >
    {props.isNode && props.subMenus.map(menu => subItems(menu))}
  </CardItems>
);
const LeafMenuItem = (props: Props) => (
  <ItemSub
    text={props.title}
    link={props.link}
    onClick={props.onClick}
    key={props.key}
  />
);
const MenuItem = (props: Props) => (
  <>
    {props.order === 1 && <FirstOrderMenuItem {...props} />}
    {props.order === 2 && <SecondOrderMenuItem {...props} />}
    {props.order === 3 && <ThirdOrderMenuItem {...props} />}
    {props.order === 4 && <LeafMenuItem {...props} />}
  </>
);
MenuItem.defaultProps = { subMenus: [] };
export default MenuItem;
