import React, { Suspense, lazy } from 'react';
import { Route, Switch, Router } from 'react-router-dom';
import history from '../utils/history';
import Layout from '../components/Layout/Layout';
import MessageHandler from '../components/MessageHandler/MessageHandler';
import Loader from '../components/Loader/Loader';

const Dash = lazy(() => import('../features/DashBoard'));
const Login = lazy(() => import('../features/Login'));
const Trans = lazy(() => import('../features/TranVir'));
const Accounts = lazy(() => import('../features/Accounts'));

export default () => (
  <Suspense fallback={<div>Chargement...</div>}>
    <MessageHandler />
    <Loader />
    <Router history={history}>
      <Switch>
        <Route exact path="/(login|)" render={props => <Login {...props} />} />
        <Layout>
          <Route
            exact
            path="/dashboard"
            render={props => <Dash {...props} />}
          />
          <Route
            exact
            path="/transfers"
            render={props => <Trans {...props} />}
          />
          <Route
            exact
            path="/accounts"
            render={props => <Accounts {...props} />}
          />
        </Layout>
      </Switch>
    </Router>
  </Suspense>
);
