import { actionCreator } from '../../utils/generators';
import OperationsActionTypes from './operations.types';
import produce from 'immer';

const defaultState = {
  isLoading: false,
  error: null,
  operations: [],
};
const operationsReducer = produce((draft, action) => {
  switch (action.type) {
    case actionCreator('req', OperationsActionTypes.GET_OPERATIONS):
      draft.error = null;
      draft.loading = true;
      break;
    case actionCreator('res', OperationsActionTypes.GET_OPERATIONS):
      let operations = action.payload.data;
      operations.forEach(operation => {
        const date = [
          operation.date.substring(0, 4),
          operation.date.substring(4, 6),
          operation.date.substring(6, 8),
        ].join(',');
        operation.date = new Date(date);
      });
      draft.operations = operations.slice();
      draft.error = null;
      draft.isLoading = false;
      break;
    case actionCreator('fail', OperationsActionTypes.GET_OPERATIONS):
      draft.error = 'error found';
      draft.isLoading = false;
      break;
    default:
      break;
  }
}, defaultState);

export default operationsReducer;
