import React from 'react';
import { connect } from 'react-redux';
import Modal from '../Ui/CustomModel/Modal';
import { hideMessage } from '../../redux/messages/messages.actions';
import { t } from '../../i18n';

class MessageHandler extends React.Component {
  _handleClose = () => {
    this.props.hideMessage();
  };

  render() {
    return (
      <Modal
        show={this.props.shown}
        type={this.props.severity}
        text={this.props.text}
        handleClose={this._handleClose}
        title={
          this.props.severity
            ? t(`Components:modal.title.${this.props.severity}`)
            : ''
        }
        buttonPosition={
          this.props.severity === 'ERROR' || this.props.severity === 'WARN'
            ? 'FOOTER'
            : 'BODY'
        }
        hasValidateBtn={true}
        validateTitle={this.props.validateTitle}
        handleValidate={
          this.props.handleValidate
            ? this.props.handleValidate
            : this._handleClose
        }
        hasCancelBtn={this.props.hasCancelBtn}
      />
    );
  }
}

const mapDispatchToProps = dispatch => ({
  hideMessage: () => {
    dispatch(hideMessage());
  },
});
const mapStateToProps = state => ({
  shown: state.Messages.shown,
  text: state.Messages.text,
  severity: state.Messages.severity,
  hasCancelBtn: state.Messages.hasCancelBtn,
  validateTitle: state.Messages.validateTitle,
  handleValidate: state.Messages.handleValidate,
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(MessageHandler);
