import { actionCreator } from '../../utils/generators';
import types from './beneficiaries.types';
import produce from 'immer';

const defaultState = {
  isLoading: false,
  error: null,
  allBeneficiaries: [],
  transferBeneficiaries: [],
  cashTransferBeneficiaries: [],
};

const beneficiariesReducer = produce((draft, action) => {
  switch (action.type) {
    case actionCreator('req', types.DELETE_BENEFICIARY):
    case actionCreator('req', types.EDIT_BENEFICIARY):
    case actionCreator('req', types.GET_TRANSFER_BENEFICIARIES):
    case actionCreator('req', types.GET_CASH_TRANSFER_BENEFICIARIES):
    case actionCreator('req', types.GET_ALL_BENEFICIARIES):
      draft.error = null;
      draft.isLoading = true;
      break;
    case actionCreator('fail', types.GET_ALL_BENEFICIARIES):
    case actionCreator('fail', types.GET_TRANSFER_BENEFICIARIES):
    case actionCreator('fail', types.GET_CASH_TRANSFER_BENEFICIARIES):
    case actionCreator('fail', types.DELETE_BENEFICIARY):
    case actionCreator('fail', types.EDIT_BENEFICIARY):
      draft.error = 'error found';
      draft.isLoading = false;
      break;
    case actionCreator('res', types.GET_ALL_BENEFICIARIES):
      draft.error = null;
      draft.isLoading = false;
      draft.allBeneficiaries = action.payload.data;
      break;
    case actionCreator('res', types.GET_TRANSFER_BENEFICIARIES):
      draft.error = null;
      draft.isLoading = false;
      draft.transferBeneficiaries = action.payload.data;
      break;
    case actionCreator('res', types.GET_CASH_TRANSFER_BENEFICIARIES):
      draft.error = null;
      draft.isLoading = false;
      draft.cashTransferBeneficiaries = action.payload.data;
      break;
    case actionCreator('res', types.DELETE_BENEFICIARY):
      let beneficiaryId = action.payload;
      let currentBeneficiaries = Object.values(
        JSON.parse(JSON.stringify(draft.allBeneficiaries)),
      );
      draft.allBeneficiaries = currentBeneficiaries
        .filter(beneficiary => beneficiary.beneficiaryId !== beneficiaryId)
        .slice();
      draft.error = null;
      draft.isLoading = false;
      break;
    case actionCreator('res', types.EDIT_BENEFICIARY):
      let editedBeneficiary = action.payload.data;
      draft.allBeneficiaries.forEach((beneficiary, index) => {
        if (beneficiary.beneficiaryId === editedBeneficiary.beneficiaryId) {
          draft.allBeneficiaries[index] = editedBeneficiary;
        }
      });
      draft.error = null;
      draft.isLoading = false;
      break;
  }
}, defaultState);

export default beneficiariesReducer;
