import { actionCreator } from '../../utils/generators';
import LoginActionTypes from './login.types';

export const login = loginData => ({
  type: actionCreator('req', LoginActionTypes.LOGIN_ACTION),
  payload: loginData,
});

export const logout = () => ({
  type: actionCreator('req', LoginActionTypes.LOGOUT_ACTION),
});
export const toggleResetPassword = () => ({
  type: LoginActionTypes.TOGGLE_RESET_PASSWORD_MODAL,
});
export const previousResetPasswordStep = () => ({
  type: LoginActionTypes.PREVIOUS_RESET_PASSWORD_STEP,
});
export const resetPassword = resetPasswordData => ({
  type: actionCreator('req', LoginActionTypes.RESET_PASSWORD),
  payload: resetPasswordData,
});

export const confirmResetPassword = resetPasswordConfirmData => ({
  type: actionCreator('req', LoginActionTypes.CONFIRM_RESET_PASSWORD),
  payload: resetPasswordConfirmData,
});
