import { put, takeLatest, call, select, all } from 'redux-saga/effects';
import Accounts from '../../services/apis/accounts';
import { actionCreator } from '../../utils/generators';
import { getSelectedAccountNumber } from './accounts.selectors';
import types from './accounts.types';

function* getAccounts(action) {
  try {
    const accounts = yield call(new Accounts().getAccounts);
    yield put({
      type: actionCreator('res', types.GET_ACCOUNTS),
      payload: accounts,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.GET_ACCOUNTS),
      }),
    ]);
  }
}

function* getSignaletic() {
  try {
    const accountNumber = yield select(getSelectedAccountNumber);
    const signaletic = yield call(new Accounts().getSignaletic, accountNumber);
    yield put({
      type: actionCreator('res', types.GET_SIGNALETIC),
      payload: signaletic,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.GET_SIGNALETIC),
      }),
    ]);
  }
}

export function* accountsSagas() {
  yield takeLatest(actionCreator('req', types.GET_ACCOUNTS), getAccounts);
  yield takeLatest(actionCreator('req', types.GET_SIGNALETIC), getSignaletic);
}
