import { put, takeLatest, call, all } from 'redux-saga/effects';
import { actionCreator } from '../../utils/generators';
import types from './beneficiaries.types';
import Beneficiaries from '../../services/apis/beneficiaries';
import { sagaWithLoader } from '../template/sagaWithLoader';

function* getAllBeneficiaries() {
  try {
    const response = yield call(new Beneficiaries().getAll);
    yield put({
      type: actionCreator('res', types.GET_ALL_BENEFICIARIES),
      payload: response,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.GET_ALL_BENEFICIARIES),
      }),
    ]);
  }
}
function* getTransferBeneficiaries() {
  try {
    const response = yield call(new Beneficiaries().getTransferBeneficiaries);
    yield put({
      type: actionCreator('res', types.GET_TRANSFER_BENEFICIARIES),
      payload: response,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.GET_TRANSFER_BENEFICIARIES),
      }),
    ]);
  }
}
function* getCashTransferBeneficiaries() {
  try {
    const response = yield call(
      new Beneficiaries().getCashTransferBeneficiaries,
    );
    yield put({
      type: actionCreator('res', types.GET_CASH_TRANSFER_BENEFICIARIES),
      payload: response,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.GET_CASH_TRANSFER_BENEFICIARIES),
      }),
    ]);
  }
}
function* deleteBeneficiary(deleteBeneficiaryAction) {
  try {
    yield call(
      new Beneficiaries().deleteBeneficiary,
      deleteBeneficiaryAction.payload,
    );
    yield put({
      type: actionCreator('res', types.DELETE_BENEFICIARY),
      payload: deleteBeneficiaryAction.payload,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.DELETE_BENEFICIARY),
      }),
    ]);
  }
}
function* editBeneficiary(beneficiary) {
  try {
    const response = yield call(
      new Beneficiaries().editBeneficiary,
      beneficiary,
    );
    yield put({
      type: actionCreator('res', types.EDIT_BENEFICIARY),
      payload: response,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.EDIT_BENEFICIARY),
      }),
    ]);
  }
}

export function* beneficiariesSagas() {
  yield takeLatest(
    actionCreator('req', types.GET_ALL_BENEFICIARIES),
    sagaWithLoader,
    getAllBeneficiaries,
  );
  yield takeLatest(
    actionCreator('req', types.GET_TRANSFER_BENEFICIARIES),
    sagaWithLoader,
    getTransferBeneficiaries,
  );
  yield takeLatest(
    actionCreator('req', types.GET_CASH_TRANSFER_BENEFICIARIES),
    sagaWithLoader,
    getCashTransferBeneficiaries,
  );
  yield takeLatest(
    actionCreator('req', types.EDIT_BENEFICIARY),
    sagaWithLoader,
    editBeneficiary,
  );
  yield takeLatest(
    actionCreator('req', types.DELETE_BENEFICIARY),
    sagaWithLoader,
    deleteBeneficiary,
  );
}
