import LoginActionTypes from './login.types';
import { actionCreator } from '../../utils/generators';

const defaultState = {
  isLoading: false,
  isConnected: false,
  error: null,
  hash: '',
  activityId: '',
  showResetPasswordModal: false,
  activeStep: 1,
};

const loginReducer = (state = defaultState, action) => {
  switch (action.type) {
    case actionCreator('req', LoginActionTypes.LOGIN_ACTION):
      return { ...state, isLoading: true };
    case actionCreator('res', LoginActionTypes.LOGIN_ACTION):
      return {
        ...state,
        error: null,
        isLoading: false,
        isConnected: true,
      };
    case actionCreator('fail', LoginActionTypes.LOGIN_ACTION):
      return {
        ...state,
        error: 'error found',
        isLoading: false,
      };
    case actionCreator('req', LoginActionTypes.LOGOUT_ACTION):
      return {
        ...state,
        error: null,
        isLoading: true,
      };
    case actionCreator('res', LoginActionTypes.LOGOUT_ACTION):
      return {
        ...state,
        error: null,
        isLoading: false,
        isConnected: false,
      };
    case actionCreator('fail', LoginActionTypes.LOGOUT_ACTION):
      return {
        ...state,
        error: 'error found',
        isLoading: false,
        isConnected: false,
      };
    case actionCreator('req', LoginActionTypes.RESET_PASSWORD):
    case actionCreator('req', LoginActionTypes.CONFIRM_RESET_PASSWORD):
      return {
        ...state,
        error: null,
        isLoading: true,
      };
    case actionCreator('res', LoginActionTypes.RESET_PASSWORD):
      return {
        ...state,
        hash: action.payload.data.hash,
        activityId: action.payload.data.activityId,
        error: null,
        isLoading: false,
        activeStep: 2,
      };
    case actionCreator('res', LoginActionTypes.CONFIRM_RESET_PASSWORD):
      return {
        ...state,
        error: null,
        isLoading: false,
      };
    case actionCreator('fail', LoginActionTypes.RESET_PASSWORD):
    case actionCreator('fail', LoginActionTypes.CONFIRM_RESET_PASSWORD):
      return {
        ...state,
        error: action.payload.text,
        isLoading: false,
      };
    case LoginActionTypes.TOGGLE_RESET_PASSWORD_MODAL:
      return {
        ...state,
        showResetPasswordModal: !state.showResetPasswordModal,
        activeStep: 1,
      };
    case LoginActionTypes.PREVIOUS_RESET_PASSWORD_STEP:
      return {
        ...state,
        activeStep: 1,
      };
    default:
      return state;
  }
};

export default loginReducer;
