import types from './menu.types';
import produce from 'immer';

const defaultState = {
  accountsActiveTab: 'operations',
  transfersActiveTab: 'favorites',
};

const menuReducer = produce((draft, action) => {
  switch (action.type) {
    case types.ACTIVATE_ACCOUNTS_TAB:
      draft.accountsActiveTab = action.payload;
      break;
    case types.ACTIVATE_TRANSFERS_TAB:
      draft.transfersActiveTab = action.payload;
      break;
    default:
      break;
  }
}, defaultState);
export default menuReducer;
