import React from 'react';
import { connect } from 'react-redux';
import { hideMessage } from '../../redux/messages/messages.actions';
import { LoaderBodyContainer } from './Loader.styles';
import LoaderArt from '../Ui/LoaderArt/LoaderArt';
import BackDrop from '../Ui/BackDrop/BackDrop';

class Loader extends React.Component {
  _handleClose = () => {
    this.props.hideMessage();
  };

  render() {
    return (
      <>
        <BackDrop show={this.props.shown} clicked={this._handleClose} />
        <LoaderBodyContainer show={this.props.shown}>
          <LoaderArt />
        </LoaderBodyContainer>
      </>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  hideMessage: () => {
    dispatch(hideMessage());
  },
});
const mapStateToProps = state => ({
  shown: state.Loader.shown,
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Loader);
